body {
  margin: 0;
  padding: 65px 0 0;
  font-family: sans-serif;
  height: 100%;
}

#signout {
  color: #2196f3;
  cursor: pointer;
}

#navig {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.card-scroll {
  height: 300px;
  overflow: scroll;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.dog-list {
  -webkit-column-gap: 1.5em;
          column-gap: 1.5em;
  font-size: 0.85em;
  margin: 1.5em 0;
  padding: 0;
  display: inline-block;
}

@media only screen and (min-width: 992px) {
  .navbar-collapse {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
  }
}

@media only screen and (min-width: 767px) {
  .dog-list {
    -webkit-column-count: 2;
    column-count: 2;
  }
}

@media only screen and (min-width: 991px) {
  .dog-list {
    -webkit-column-count: 3;
    column-count: 3;
  }
}

@media only screen and (min-width: 767px) {
  .my-articles-list {
    -webkit-column-count: 2;
    column-count: 2;
  }
}

.dog:hover {
  opacity: 0.75;
}

.dog-modal {
  text-align: center;
  margin: 0 auto;
  width: 80%;
}

.preview-title {
  top: 40%;
  background: rgba(255, 255, 255, 0.4);
  margin: 0 auto;
  width: 100%;
  background-size: contain;
  left: 0;
  padding: 15px;
}

.photo-article-preview:hover {
  opacity: 0.75;
}

nav.navbar {
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  background: #fff;
}

.container {
  padding-bottom: 55px;
}

.photo-article-preview {
  height: 300px;
  width: 100%;
}

@media only screen and (min-width: 577px) {
  .photo-article-preview {
    height: 400px;
  }
}

.map-parent {
  position: relative;
  height: 400px;
  width: 80%;
}

.search-dog-friendly {
  min-width: 300px;
}



